.sidebar {
  background-color: #f2f2f2;
  width: 0;
  padding: 10px;
  position: fixed;
  top: 63px;
  bottom: 0;
  left: 0;
  height: calc(100% - 50px);
  z-index: 999;
  overflow: hidden;
  transition: width 2s ease, transform 2s ease;
}

.sidebar.open {
  width: 15%;
  transform: translateX(0);
}

.sidebar.open.slide {
  transform: translateX(15%);
}

.sidebar.close.slide {
  transform: translateX(0);
}

.sidebar-title {
  font-size: 20px;
  margin-bottom: 10px;
}

.sidebar-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-item {
  margin-bottom: 5px;
}

.sidebar-button {
  display: flex;
  align-items: center;
  width: 100%;
  padding: 5px;
  background-color: #e0e0e0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  text-decoration: none; /* Remover o sublinhado do texto */
  color: black; /* Definir a cor do texto como preto */
}

.sidebar-button:hover {
  background-color: #d0d0d0;
}

.sidebar-button:focus {
  outline: none;
}

.sidebar-icon {
  margin-right: 10px;
}
